import * as msal from "@azure/msal-browser";
import * as msalC from "@azure/msal-common";
import * as teams from "@microsoft/teams-js";
import * as log from '../logger';
import { isUnknownObject as isObject } from '../utils';
import { BrowserAuthErrorMessage } from "@azure/msal-browser";
import { IAuthMessages } from "./IAuthMessages";
import { IAuthErrorResult } from "./IAuthErrorResult";

const appId = "890c8d10-4bfd-4103-8a8d-8135accaee37"; // MSALtest

// Functional Permission
const requiredScopes = [
    "User.ReadBasic.All",       // read basic profile information (name, email) of other users
    "Team.ReadBasic.All",       // Read the names and descriptions of joined teams, on behalf of the signed-in user.
    "Channel.ReadBasic.All",    // Read channel names and channel descriptions, on behalf of the signed-in user.
    "ChannelMessage.Read.All",  // Allows an app to read a channel's messages in Microsoft Teams, on behalf of the signed-in user.
];

// Additional Permissions requested by MSAL
const additionalScopes = [
    "offline_access",   // Maintain access to data you have given it access to
    "openid",           // Sign users in
    "profile",          // View users' basic profile
];

const config: msal.Configuration = {
    auth: {
        clientId: appId,
        authority: "https://login.microsoftonline.com/common", // default is common, others:  https://login.microsoftonline.com/<tenant>  https://login.microsoftonline.com/organizations/  https://login.microsoftonline.com/consumers/
        redirectUri: window.location.origin + "/auth-end.html",
        navigateToLoginRequestUrl: false,
    },
    cache: {
        cacheLocation: "localStorage"
    },
};


const authError = (isRecoverable: boolean, needGesture: boolean, needConsent: boolean, message: string): IAuthErrorResult => ({ isRecoverable, needGesture, needConsent, userMessage: message });


/**
 * Note: can not use instanceOf with stuff extending from builtin types like Error
 * see https://github.com/Microsoft/TypeScript/wiki/Breaking-Changes#extending-built-ins-like-error-array-and-map-may-no-longer-work
 * This would probably work with ES2015/ES2016, however MSAL is compiled to ES5
 * @param err
 */
const isProbablyMsalError = (err: unknown): err is msalC.AuthError => isObject(err) && 'errorCode' in err && 'errorMessage' in err;


const inspectError = (err: unknown, action: string): void | never => {
    if (isProbablyMsalError(err)) {
        log.warn(`${action}() failed with code ${err.errorCode}: ${err.errorMessage}`);
        if (err.name === "InteractionRequiredAuthError") {
            throw err;
        }
    } else {
        if (isObject(err) && 'name' in err && 'message' in err) {
            log.warn(`${action}() failed with ${err.name}: ${err.message}`);
        } else {
            log.error(`${action}() failed for unknown reason`);
        }
        throw err;
    }
}


export const loginHintKey = "tased_msalLoginHint";
export const client = new msal.PublicClientApplication(config);


export function getAuthParameters(hint?: string | null): msalC.AuthorizationUrlRequest {
    const params: msalC.AuthorizationUrlRequest = { scopes: [...requiredScopes] };

    if (hint) params.loginHint = hint;

    return params;
}


function getSilentRequest(account: msal.AccountInfo): msal.SilentRequest {
    const params: msal.SilentRequest = {
        scopes: [appId, ...requiredScopes],
        forceRefresh: false, // Set this to "true" to skip a cached token and go to the server to get a new token
        account,
    };

    return params;
}


class ScopesNotGrantedError extends Error {

    scopes: string[];

    constructor(scopes: string[]) {
        super(`Required permissions were not granted: ${scopes.join(", ")}`);
        this.scopes = scopes;
    }
}


class ConsentError extends Error {
    public reason: string;

    constructor(reason: string) {
        super(`Error while trying to get adminsitrator consent`);
        this.reason = reason;
    }
}


class ConsentNotGrantedError extends ConsentError {
    constructor() {
        super(`Required administrator consent was not granted`);
    }
}


/**
 * Ensures that all requested scopes were actually granted.
 * @param receivedScopes scopes from
 */
function ensureScopesC(result: msalC.AuthenticationResult): void {
    if (result.fromCache) {
        return;
    }

    ensureScopes(result.scopes);
}


function ensureScopes(scopes: string[]): void {
    const missingScopes = requiredScopes.filter(rs => !scopes.includes(rs));

    if (missingScopes.length > 0) {
        throw new ScopesNotGrantedError(missingScopes);
    }
}

const CONSENT_NOT_GRANTED = "CONSENT_NOT_GRANTED";
export const CONSENT_STATE = "ADMIN_CONSENT";

export interface ConsentResponse {
    tenant: string | null,
    scope: string[],
    adminConsent: boolean,
    error: string | null,
    errorDescription: string | null,
}

/**
 * class is only there to be able to use decorators
 */
class Auth {
    @log.traceAsync
    static hostedInTeams() {
        return new Promise<boolean>((resolve, reject) => {
            try {
                teams.initialize(() => resolve(true));
                setTimeout(() => resolve(false), 5000);
            } catch (e) {
                reject(e);
            }
        });
    }


    @log.traceAsync
    static loginPopup(hint: string): Promise<string> {
        return new Promise<string>((resolve, reject) => teams.authentication.authenticate({
            url: window.location.origin + `/auth-start.html?hint=${encodeURIComponent(hint)}`,
            width: 600,
            height: 535,
            successCallback: resolve,
            failureCallback: reject
        }));
    }


    @log.traceAsync
    static async getAuthTokenSomehow(hint: string): Promise<string> {
        const inTeams = await hostedInTeams;

        if (hint) {
            const account: msalC.AccountInfo | null = client.getAccountByUsername(hint);

            if (account) {
                // try to get the token silently via cache or refresh
                log.info(`Trying acquireTokenSilent`);
                try {
                    const re = await client.acquireTokenSilent(getSilentRequest(account));
                    ensureScopesC(re);
                    return re.accessToken;
                } catch (err) { inspectError(err, "acquireTokenSilent") }
            } else {
                log.info(`No account info cached for user [${hint}]`);
            }

            // this will probably succeed as long as the user is accessing the page from inside Teams
            log.info(`Trying ssoSilent`);
            try {
                const re = await client.ssoSilent(getAuthParameters(hint));
                ensureScopesC(re);
                return re.accessToken;
            } catch (err) { inspectError(err, "ssoSilent") }
        }

        if (inTeams) {
            try {
                const JSONre = await loginPopup(hint ?? "");
                const re = JSON.parse(JSONre) as msalC.AuthenticationResult;
                ensureScopesC(re);
                return re.accessToken;
            } catch (err) {
                inspectError(err, nameof(loginPopup));
                throw err;
            }
        } else {
            try {
                const re = await client.acquireTokenPopup(getAuthParameters(hint));
                ensureScopesC(re);
                return re.accessToken;
            } catch (err) {
                inspectError(err, "acquireTokenPopup");
                throw err;
            }
        }
    }


    static getConsentUrl(tenantId: string): string {
        const u = new URL(`https://login.microsoftonline.com/${tenantId}/v2.0/adminconsent`);

        u.searchParams.append("client_id", appId);
        u.searchParams.append("state", CONSENT_STATE);
        u.searchParams.append("redirect_uri", window.location.origin + "/auth-end.html");
        u.searchParams.append("scope", [...requiredScopes, ...additionalScopes].join(" "));

        return u.toString();
    }


    @log.traceAsync
    static async getConsent(tenantId: string): Promise<void> {
        const curl = Auth.getConsentUrl(tenantId);

        const authUrl = new URL(window.location.origin + "/auth-start.html");
        authUrl.searchParams.append("consent", curl.toString());
        let re: ConsentResponse;

        try {
            const JSONre = await new Promise<string>((successCallback, failureCallback) => teams.authentication.authenticate({ url: authUrl.toString(), successCallback, failureCallback }));
            re = JSON.parse(JSONre);
        } catch (reason) {
            if (reason instanceof ConsentError) throw reason;

            if (reason === CONSENT_NOT_GRANTED) {
                throw new ConsentNotGrantedError();
            }

            throw new ConsentError(String(reason));
        }

        if (re.error) {
            throw new ConsentError("" + re.error + '\n' + re.errorDescription);
        }

        ensureScopes(re.scope);

        /* TODO: Consent stuff
           - Consent needed test
           - UI changes
        */
    }
}


export const getConsent = Auth.getConsent;
export const loginPopup = Auth.loginPopup;
export const hostedInTeams = Auth.hostedInTeams();
export const logout = (): Promise<void> => client.logout();
export const getAuthTokenSomehow = Auth.getAuthTokenSomehow;


export function processAuthError(err: unknown, messages: IAuthMessages): IAuthErrorResult {
    /*
    Note: MSAL Error hierachy as I understand it
        msalC.AuthError
        |- msal.BrowserAuthError
        |- msal.BrowserConfigurationAuthError
        |- msalC.ClientAuthError          // Error thrown when there is an error in the client code running on the browser.
        |- msalC.ClientConfigurationError // Error thrown when there is an error in configuration of the MSAL.js library.
        |- msalC.ServerError              // Error thrown when there is an error with the server code
            |- msalC.InteractionRequiredAuthError // Error thrown when user interaction is required at the auth server.
    */

    // error from teams.authentication.authenticate() when popup blocked
    if (err === "FailedToOpenWindow") {
        return authError(true, true, false, messages.needServerInteraction);
    }

    if (err instanceof ScopesNotGrantedError) {
        return authError(true, true, true, messages.needConsent);
    }

    if (isProbablyMsalError(err)) {
        if (err.name === "InteractionRequiredAuthError") {
            if (err.errorMessage.includes("AADSTS65001")) {
                // The user or administrator has not consented to use the application with ID 'xxxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxxxx' named 'XXXX'. Send an interactive authorization request for this user and resource.
                return authError(true, true, true, messages.needConsent);
            } else {
                return authError(true, true, false, messages.needServerInteraction);
            }
        }

        switch (err.errorCode) {
            case msalC.ClientAuthErrorMessage.accountMismatchError.code:
                logout();
            // fallthrough

            case msalC.ClientAuthErrorMessage.userLoginRequiredError.code:
            case BrowserAuthErrorMessage.userCancelledError.code:
            case BrowserAuthErrorMessage.popUpWindowError.code:
                return authError(true, true, false, messages.loginMessage)
        }
    }

    return authError(false, false, false, messages.unkownError);
}
